import { useState, useEffect } from 'react'

const LottoPage = () => {
  const [lottoData, setLottoData] = useState<any>(null)
  const [loading, setLoading] = useState<boolean>(true)
  const [error, setError] = useState<string | null>(null)

  // Fetch lotto data from the API
  useEffect(() => {
    const fetchLottoData = async () => {
      try {
        const response = await fetch('/api/lotto6s')  // Adjust if API endpoint changes
        if (!response.ok) {
          throw new Error('Failed to fetch lotto data')
        }
        const data = await response.json()
        setLottoData(data)
      } catch (err) {
        setError('Error fetching lotto data.')
        console.error(err)
      } finally {
        setLoading(false)
      }
    }

    fetchLottoData()
  }, [])  // Empty dependency array ensures it runs once on mount

  if (loading) {
    return <div>Loading lotto data...</div>
  }

  if (error) {
    return <div>{error}</div>
  }

  return (
    <div className="mx-auto max-w-7xl px-4 py-6 sm:px-6 lg:px-8">
      <h1 className="text-2xl font-semibold text-gray-900">Lotto Data</h1>
      {lottoData ? (
        <div className="mt-6">
          <h2 className="text-xl font-semibold text-gray-900">Lotto Results</h2>
          {/* Render lotto data here, adjust according to your data structure */}
          <pre className="mt-2 text-sm text-gray-700">
            {JSON.stringify(lottoData, null, 2)}
          </pre>
        </div>
      ) : (
        <p>No lotto data available.</p>
      )}
    </div>
  )
}

export default LottoPage
